import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

import ShowsList from "./components/ShowsList";
import PriorityShows from "./components/PriorityShows";
import Login from "./components/Login";
import Registration from "./components/Registration";
import SeasonsList from "./components/SeasonsList";
import EpisodesList from "./components/EpisodesList";
import Episode from "./components/Episode";
import E404 from "./components/E404";

import { store } from "./store";

const routes = [
  {
    name: "priorityShows",
    path: "",
    //redirect: { name: "shows" }
    component: PriorityShows,
    beforeEnter(to, from, next) {
      store.dispatch("shows/loadPriority");
      next();
    }
  },
  {
    name: "shows",
    path: "/shows",
    component: ShowsList,
    beforeEnter(to, from, next) {
      store.dispatch("shows/loadItems");
      next();
    }
  },
  {
    //я знаю що цей кусок можна робити дитиною попереднього але незнаю чи це необхідно чи можливо враховуючи всі ці айді
    name: "seasons",
    path: "/shows/:id",
    component: SeasonsList,
    beforeEnter(to, from, next) {
      if (to.params.id !== "newshow") {
        let showTitle = to.params.id.replace(/_/g, " ");
        console.log("routrs.js seasons showTitle", showTitle);
        let show = store.getters["shows/item"](showTitle);
        console.log("routrs.js seasons show", show);
        store.dispatch("seasons/loadItems", show.tvShowId);
      }
      next();
    }
    // таким чином в мене не виходить тому що я не можу з іншого стору дістати інформацію про айді маючи назву шоу
    // можливо це не потрібно і треба відсилати саме назву і номер сезону а на бекенді з цим розбиратись Підкажи!
    // я звертаюсь на ти тому що нас попереджали що у вас так прийнято але без проблем можу говорити і на ви
    // просто дай знати як краще і на всяк випадок перепрошую!*/
  },
  {
    name: "episodes",
    path: "/shows/:id/:seasonName",
    component: EpisodesList,
    beforeEnter(to, from, next) {
      if (to.params.seasonName !== "newseason") {
        let seasonName = to.params.seasonName.replace(/_/g, " ");
        //console.log("routrs.js episodes seasonName", seasonName);
        let season = store.getters["seasons/item"](seasonName);
        //console.log("routrs.js episodes season", season);
        store.dispatch("episodes/loadItems", season.seasonId);
      }
      next();
    }
  },
  {
    name: "episode",
    path: "/shows/:id/:seasonName/:episodeName",
    component: Episode,
    beforeEnter(to, from, next) {
      store.dispatch("episodes/clearMes");
      next();
    }
  },
  {
    path: "/login",
    component: Login
  },
  {
    path: "/registration",
    component: Registration
  },
  {
    path: "*",
    component: E404
  }
];

export const router = new VueRouter({
  routes,
  mode: "history"
});



